    @font-face {
        font-family: 'Inter';
        font-style: normal;
        font-weight: 400;
        font-display: swap;
        src: url(/mesto-react-auth/static/media/Inter-Regular.4232a675.woff2) format("woff2"), url(/mesto-react-auth/static/media/Inter-Regular.3a1908c2.woff) format("woff");
    }

    @font-face {
        font-family: 'Inter';
        font-style: normal;
        font-weight: 500;
        font-display: swap;
        src: url(/mesto-react-auth/static/media/Inter-Medium.498969d1.woff2) format("woff2"), url(/mesto-react-auth/static/media/Inter-Medium.3a5b9a96.woff) format("woff");
    }

    @font-face {
        font-family: 'Inter';
        font-style: normal;
        font-weight: 900;
        font-display: swap;
        src: url(/mesto-react-auth/static/media/Inter-Black.8da0f3b5.woff2) format("woff2"), url(/mesto-react-auth/static/media/Inter-Black.6db365ae.woff) format("woff");
    }

    .root {
        font-family: 'Inter', Arial, sans-serif;
        background-color: #000000;
        width: 100%;
        margin: 0 auto;
        min-width: 160px;
        min-height: 100vh;
        text-rendering: optimizeLegibility;
        color: #FFFFFF;
      }

.header {
    max-width: 880px;
    min-height: 119px;
    border-bottom: 1px solid rgba(84, 84, 84, 0.67);
    display: flex;
    align-items: center;
    margin: 0 auto;
    justify-content: space-between;

}

@media screen and (max-width: 900px) {
    .header {
        min-height: 84px;
    }
}

.header__logo {
    width: 142px;
    height: 33px;
    /*background-image: url(../../../images/Logo.svg);*/
    opacity: 1;
    transition: opacity 1s linear .3s;
    background-size: cover;
}

.header__logo:hover {
    opacity: .8;
}

@media screen and (max-width: 900px) {
    .header__logo {
        margin-left: 27px;
        width: 103px;
        height: 24px;
       
    }
}

.profile {
    max-width: 880px;
    display: flex;
    margin: 36px auto 47px auto;
    flex-direction: row;
    align-items: center;
}

@media screen and (max-width: 900px) {
    .profile {
        flex-direction: column;
        margin-bottom: 36px;
    }
}

.profile__avatar {
    border-radius: 50%;
    width: 120px;
    height: 120px;
}

@media screen and (max-width: 900px) {
    .profile__avatar {
        margin-bottom: 22px;
    }
}

.profile__biography {
    display: flex;
    margin-left: 10px;
    width: 354px;
    margin-top: 26px;
}

@media screen and (max-width: 900px) {
    .profile__biography {
        margin: 0;
        width: 282px;
    }
}

.profile__info {
    margin: 0 0 0 20px;
}

@media screen and (max-width: 900px) {
    .profile__info {
        margin: 12px 10px 36px 30px;
        flex-direction: column;
    }
}

    .profile__name {
        max-width: 294px;
        font-weight: 500;
        font-size: 42px;
        line-height: 1.1;
        margin: 0;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
    
    @media screen and (max-width: 900px) {
        .profile__name {
            text-align: center;
            font-size: 27px;
            line-height: 1.2;
            width: 189px;
        }
    }
.profile__profession {
    width: 282px;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 1.2;
    margin-top: 16px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

@media screen and (max-width: 900px) {
    .profile__profession {
        text-align: center;
        font-size: 14px;
        line-height: 1.2;
        margin-top: 14px;
        margin-bottom: 0;
        width: 189px;
        height: 14px;
    }
}
.profile__edit-button {
    background-image: url(/mesto-react-auth/static/media/profile.6bfdb354.svg);
    background-position: center;
    background-repeat: no-repeat;
    width: 24px;
    height: 24px;
    padding: 0;
    margin-top: 15px;
    cursor: pointer;
    background-color: black;
    border: 1px solid white;
    margin-left: 18px;
}

.profile__edit-button:hover {
    opacity: .5;
}

@media screen and (max-width: 900px) {
    .profile__edit-button {
        width: 18px;
        height: 18px;
        margin-left: 0;
        margin-top: 20px;
    }
}

.profile__add-button {
    background-image: url(/mesto-react-auth/static/media/add.b2fce4ac.svg);
    background-position: center;
    background-repeat: no-repeat;
    width: 150px;
    height: 50px;
    padding: 0;
    cursor: pointer;
    margin-left: 250px;
    background-color: black;
    border: 2px solid white;
    justify-content: end;
}

.profile__add-button:hover {
    opacity: .6;
}

@media screen and (max-width: 900px) {
    .profile__add-button {
        width: 282px;
        margin: 0;
    }
}

.profile__avatar-button {
  display: block;
  position: relative;
  cursor: pointer;
  border-radius: 50%;
  align-items: center;
  justify-content: center;
  margin-top: 15px;
  margin-left: 18px;
  opacity: 1;
  background-color: #000;
  border: none;
}

.profile__avatar-button:after {
  width: 100%;
  height: 100%;
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  background-image: url(/mesto-react-auth/static/media/profile.6bfdb354.svg);
  background-color: #000;
  background-repeat: no-repeat;
  background-position: center;
  opacity: 0;
  transition: opacity .3s;
  background-size: 26px 26px;
}

.profile__avatar-button:hover:after {
  opacity: 0.6;
}
.cards {
    max-width: 880px;
    display: grid;
    grid-row-gap: 20px;
    row-gap: 20px;
    grid-template-columns: repeat(auto-fill, 280px);
    grid-column-gap: 18px;
    grid-row-gap: 21px;
    margin: auto;
    padding: 0;
    justify-content: center;
    list-style-type: none;
    min-height: 30vh;
}

.card {
    width: 100%;
    display: flex;
    position: relative;
    flex-direction: column;
    justify-content: center;
}

.card__image {
    border-radius: 10px 10px 0 0;
    max-width: 282px;
    height: 282px;
    cursor: pointer;
}

.card__location {
    display: flex;
    background-color: white;
    border-radius: 0 0 10px 10px;
    justify-content: space-between;
}

.card__title {
    max-width: 200px;
    margin: 25px 21px 31px 15px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: 24px;
    line-height: 1.2;
    font-weight: 900;
    color: #000;
    justify-content: space-between;
}


.card__like-button {
    background-image: url(/mesto-react-auth/static/media/Like.00b1c22a.svg);
    background-position: center;
    background-repeat: no-repeat;
    min-width: 22px;
    height: 19px;
    background-color: white;
    border: 0;
    margin-top: 22px;
    padding: 0;
    cursor: pointer;
    background-size: 100%;
}

.card__like-button:hover {
    opacity: .5;
}

.card__like-button_active {
    background-image: url(/mesto-react-auth/static/media/Like-active.3d813f81.svg);
}

.card__delete-button {
    width: 18px;
    height: 19px;
    background-image: url(/mesto-react-auth/static/media/Trash.4220563a.svg);
    position: absolute;
    top: 18px;
    right: 15px;
    color: #fff;
    background-color: transparent;
    border: none;
    padding: 0;
    background-position: center;
    cursor: pointer;
    background-repeat: no-repeat;
}

.card__delete-button:hover {
    opacity: .6;
}

.card__likes {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 22px;
    width: 100%;
    margin-right: 20px;
}
.card__like-number {
    font-size: 13px;
    line-height: 16px;
    font-weight: 400;
    text-align: center;
    color: black;
    padding-top: 3px;
}
.footer {
    margin: 67px auto 0 auto;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    max-width: 880px;
    color: #545454;
    /* position: fixed;
    top: 80%;
    left: 15%; */
    padding: 60px;
}

@media screen and (max-width: 900px) {
    .footer {
        margin: 48px 19px 0;
        padding-bottom: 36px;
    }
}

.footer__copyright {
    margin: 0;
    font-size: 18px;
    line-height: 1.1;
}

@media screen and (max-width: 900px) {
    .footer__copyright {
        font-size: 14px;
        line-height: 1.2;
    }
}

.popup {
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    position: fixed;
    background-color: rgba(0, 0, 0, .5);
    align-items: center;
    justify-content: center;
    display: flex;
    z-index: 5;
    visibility: hidden;
    opacity: 0;
    transition: visibility .5s, opacity .3s  linear;
}

.popup_active {
    display: flex;
    visibility: visible;
    opacity: 1;
}

.popup__container {
    border-radius: 10px;
    position: relative;
    background: #fff;
    width: 358px;
    padding: 34px 36px 37px 36px;
}

@media screen and (max-width: 900px) {
    .popup__container {
        width: 238px;
        padding: 25px 22px;
    }
}

.popup__close-button {
    width: 40px;
    height: 40px;
    background-image: url(/mesto-react-auth/static/media/Close.d8210d64.svg);
    position: absolute;
    top: -40px;
    right: -40px;
    color: #fff;
    background-color: transparent;
    border: none;
    padding: 0;
    background-position: center;
    cursor: pointer;
    background-repeat: no-repeat;
   }

.popup__close-button:hover {
    opacity: .6;
}

@media screen and (max-width: 900px) {
    .popup__close-button {
        width: 30px;
        height: 30px;
        background-size: 20px;
        top: -40px;
        right: -5px;
    }
}

.popup__title {
    font-size: 24px;
    font-weight: 900;
    line-height: 29px;
    color: #000;
    margin: 0;
    overflow: hidden;
    text-overflow: ellipsis;
}

@media screen and (max-width: 900px) {
    .popup__title {
        font-size: 18px;
        line-height: 22px;
        margin-bottom: 65px;
    }
}

.popup__form {
    margin-top: 54px;
}
.popup__form_type_delete {
    margin-top: 13px;
}
.popup__input {
    font-size: 14px;
    line-height: 17px;
    color: #000;
    box-sizing: border-box;
    border: none;
    width: 100%;
    border-bottom: 1px solid rgba(0, 0, 0, .15);
    margin-bottom: 28px;
    padding-bottom: 10px;
}
.popup__save-button {
    text-align: center;
    width: 358px;
    height: 50px;
    background-color: #000;
    color: #fff;
    font-size: 18px;
    line-height: 22px;
    cursor: pointer;
    border: none;
    margin-top: 18px;
}

.popup__save-button:hover {
    opacity: .8;
}

@media screen and (max-width: 900px) {
    .popup__save-button {
        width: 238px;
        height: 46px;
        font-size: 14px;
        line-height: 17px;
        margin-top: 15px;
    }
}

.popup__container-image {
    position: relative;
    max-width: 816px;
    max-height: 562px;
}

@media screen and (max-width: 320px) {
    .popup__container-image {
        max-width: 240px;
        max-height: 321px;
    }
}
.popup__image {
    object-fit: cover;
    max-width: 75vw;
    max-height: 75vh;
    }

.popup__title-image {
    font-size: 12px;
    font-weight: 400;
    line-height: 29px;
    color: #fff;
    margin-top: 0;
    left: 0;
    position: absolute;
}
.error-message {
    opacity: 0;
}
.error-message_visible {
    opacity: 1;
    padding-top: 5px;
    font-size: 12px;
    font-weight: 400;
    line-height: 14,52px;
    color: #f00;
}
.popup__input_type_error {
    border-color: #F00;
    margin-bottom: 0px; 
}
.popup__save-button_disabled {
    border: 1px solid rgba(0, 0, 0, 0.2);
    background-color: #fff;
    cursor: default;
    color: rgba(0, 0, 0, 0.2);
}
